import React, { Component, createRef } from 'react';
import AChild from './AChild'; 

export default class AParent extends Component {
	constructor (props) {
		super(props);
		//用于调用子组件函数-1
		this.test01Ref = createRef();
	}
	state = {
		msg1: 'h111',
		msg2: 'h222',
	}
	fn = () => {
		console.log('fn', this.state);
		//用于调用子组件函数-2
		this.test01Ref.current.testfn(this.state);
	}
	// 渲染函数
    render() {
    	return (
			<div>
				<div onClick={this.fn}>AParent:点击触发子组件事件</div>
				<AChild ref={this.test01Ref} ms1={this.state.msg1} ms2={this.state.msg2}></AChild>
			</div>
		);
    }
};
