import  { useState } from 'react';
import EChild1 from './EChild1'; 
import EChild2 from './EChild2'; 
import CounterContext1 from './context.js';
//const CounterContext1 = createContext(null);

function EParent() {
	const [count1, setCount1] = useState(11);
	const [count2, setCount2] = useState(66);
	return (
		<div>
			<CounterContext1.Provider value={{ count1, setCount1, count2, setCount2 }}>
			    {count1}:{count2}
			    <EChild1></EChild1>
				<EChild2></EChild2>
			</CounterContext1.Provider>
		</div>
	);
}

export default EParent