import { useImperativeHandle, forwardRef } from 'react'; 

let CChild = (props, ref)=>{
	console.log('CChild', 'render');
	//在父级中调用 cFn
	useImperativeHandle(ref, () => ({
        cFn: (params)=>{
            //需要处理的数据
            console.log('CChild', params)
        }
    }))
	return (
		<div>
			<div>CChild:ms1:{props.ms1}</div>
			<div>CChild:ms2:{props.ms2}</div>
		</div>
	);
};

CChild = forwardRef(CChild);
export default CChild 