import { memo } from 'react';

const Foo = memo((props) => {
	console.log('Foo', "函数重新渲染");
	return <span>Foo</span>
});
//父组件 render 时,该组件不会重新 render
const UseMemo02 = memo((props) => {
	console.log('UseMemo02', 'render');
	return (
		<div>
			UseMemo02:
			<Foo></Foo>
		</div>
	);
});

export default UseMemo02 