import { useMemo } from 'react';

const UseMemo03 = (props = {}) => {
    console.log('UseMemo03', 'render', 1);//父组件 render 时,该函数[会]触发;
    return useMemo(() => {
        console.log('UseMemo03', 'render', 2);//父组件 render 时,该函数[不会]触发;
        return <div>UseMemo03:</div>
    }, []);
};

export default UseMemo03