import { useMemo } from 'react';

const UseMemo04 = (props = {}) => {
    console.log('UseMemo04', 'render', 1);//父组件 render 时,该函数[会]触发;
    return useMemo(() => {
        console.log('UseMemo04', 'render', 2);//父组件 render 时,该函数[会]触发;(因为依赖的 total1 变化了)
        return <div>UseMemo04:{props.total1}</div>
    }, [props.total1]);
};

export default UseMemo04