import { useSelector, useDispatch } from 'react-redux';
import { saveLoginUser, saveLoginPerm } from '../../../store/store1/action';

let FChild2 = (props, ref)=>{
	const orderSum = useSelector(state => state.loginUser.orderSum);
    const dispatch = useDispatch();

	const handleClick1 = () => {
		console.log('FChild2', 'handleClick1');
		dispatch(saveLoginUser('orderSum44', 'orderSum'));
	}
	return (
		<div>
			<div onClick={handleClick1}>FChild2:点击事件:{orderSum}</div>
		</div>
	);
};

export default FChild2 