import React, { Component } from 'react';
import BChild from './BChild'; 

export default class BParent extends Component {
	state = {
		msg1: 'h111',
		msg2: 'h222',
	}
	pFn = (params) => {
		console.log('pFn', this.state, params);
	}
	// 渲染函数
    render() {
    	return (
			<div>
				<div>BParent:{this.state.msg1}:{this.state.msg2}</div>
				<BChild fn1={this.pFn}></BChild>
			</div>
		);
    }
};
