import * as home from './action-type';

// 保存登录用户信息
export const saveLoginUser = (value, datatype) => {
	console.log('saveLoginUser', value, datatype)
	return {
		type: home.SAVELOGINUSER,
		value,
		datatype,
	}
}

// 保存登录用户权限
export const saveLoginPerm = (value, datatype) => {
	return {
		type: home.SAVELOGINPERM,
		value,
		datatype,
	}
}